import * as React from 'react';
import * as ReactDOM from 'react-dom';
import KendoBaseComponent, { KendoWidgetProps } from '@progress/kendo-base-component-react-wrapper';

export interface SchedulerProps extends KendoWidgetProps, kendo.ui.SchedulerOptions {
    children?: React.ReactNode;
}

export default class Scheduler extends KendoBaseComponent<SchedulerProps, {}> {
    componentDidMount(): void {
        const domNode = ReactDOM.findDOMNode(this as React.ReactInstance);
        const { children, widgetRef, ...props } = this.props;

        if (domNode) {
            this.widgetInstance = new kendo.ui.Scheduler(domNode as Element, this.props);
            super.componentDidMount();
        }
    }

    componentWillReceiveProps(nextProps: SchedulerProps) {
        const { children, widgetRef, dataSource, ...props } = nextProps;

        this.widgetInstance.setOptions(props);
        const widget = (this.widgetInstance as kendo.ui.Scheduler);

        if (widget.options.dataSource !== dataSource) {
            widget.setDataSource(dataSource);
        }
    }

    render(): any {
        const domChild = React.Children
            .toArray(this.props.children)
            .filter((child: React.ReactElement<any>) => child && (child.type === 'div'))
            .shift();

        return (domChild || <div />);
    }
}


