/// <reference types="@progress/kendo-ui" />

import * as React from 'react';
import * as ReactDOM from 'react-dom';

export interface KendoWidgetProps {
    widgetRef?(widget: kendo.ui.Widget): void;
}

class KendoBaseComponent<P extends KendoWidgetProps , S = {}> extends React.PureComponent<P, {}> {
    destroyed: boolean;
    widgetInstance: kendo.ui.Widget;
    widgetDestroy: Function;

    componentDidMount(): void {
        this.exposeWidget();
        this.overrideDestroy();
    }

    componentWillUnmount(): void {
        if (!this.destroyed) {
            this.widgetDestroy.call(this.widgetInstance);
        }

        this.exposeWidget();
    }

    private exposeWidget(): void {
        if (this.props.widgetRef && typeof this.props.widgetRef === 'function') {
            (this.props.widgetRef as (widget: kendo.ui.Widget) => void)(this.widgetInstance);
        }
    }

    private overrideDestroy(): void {
        this.widgetDestroy = this.widgetInstance.destroy;

        this.widgetInstance.destroy = () => {
            this.destroyed = !this.destroyed;
            this.widgetDestroy.call(this.widgetInstance);
        };
    }
}

export default KendoBaseComponent;


